* {
  box-sizing: border-box;
}
body {
  width: 100%;
  height: 100vh;
  display: flex;
  margin: 0;
  justify-content: center;
  align-items: center;
  background-color: #16acea;
}
.btn {
  background-color: #ffffff;
  color: #d71b3b;
  font-family: 'Montserrat', sans-serif;
  text-transform: uppercase;
  margin: 15px;
  padding: 5px;
  border: 2px solid #d71b3b;
  border-radius: 50px;
  box-shadow: 0 12px #971228, 0 18px rgba(0, 0, 0, 0.4);
  font-size: 60px;
  width: 90px;
  height: 80px;
  cursor: pointer;
  position: relative;
  top: 0;
}
.disabled .btn {
  cursor: default;
}
.disabled .btn:hover {
  top: 0;
  background: #ffffff;
  color: #d71b3b;
  box-shadow: 0 12px #971228, 0 18px rgba(0, 0, 0, 0.4);
}

.btn.disabled {
  cursor: default;
}
.btn.disabled:hover {
  top: 0;
  background: #ffffff;
  color: #d71b3b;
  box-shadow: 0 12px #971228, 0 18px rgba(0, 0, 0, 0.4);
}

.btn:hover {
  background-color: #d71b3b;
  color: #16acea;
  top: 4px;
  box-shadow: 0 8px #971228, 0 14px rgba(0, 0, 0, 0.4);
}
.btn:active {
  top: 8px;
  box-shadow: 0 4px #971228, 0 10px rgba(0, 0, 0, 0.4);
}

.game {
  display: flex;
  flex-wrap: wrap;
}
.game-info {
  margin: 15px;
  color: #ffffff;
}

.board > div {
  display: flex;
}

.start {
  border: none;
  color: #d71b3b;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  margin: 4px 2px;
  cursor: pointer;
  background-color: #ffffff;
  border: none;
  border-radius: 15px;
  font-weight: 600;
}

.start:hover {
  background-color: #d71b3b;
  color: #ffffff;
}

